import React from 'react';
import PropTypes from 'prop-types';
import { Field } from 'redux-form';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './APAutoTabInput.css';

const baseClass = 'ap-auto-tab-input';

class InnerInput extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      values: Array(props.numberOfInputs).fill('')
    };
  }

  handleChange = (e, index) => {
    const target = e.target;
    const value = target.value;
    const { maxLength } = this.props;
    let newValues = this.state.values.slice();

    if (value.length <= maxLength) {
      newValues[index] = value;
      this.setNewValues(newValues);
    } else if (
      value.length > maxLength &&
      index < this.props.numberOfInputs - 1
    ) {
      newValues[index + 1] = value.substr(value.length - 1, 1);
      this.setNewValues(newValues);
    }
  };

  setNewValues(newValues) {
    this.setState({ values: newValues }, () => {
      this.props.input.onChange(this.state.values.join(''));
    });
  }

  handleKeyUp = (e, index) => {
    const backspaceCode = 8;
    if (e.keyCode === backspaceCode && e.target.value.length === 0) {
      let prev = e.target.previousElementSibling;
      while (prev) {
        if (prev === null) break;
        if (prev.tagName.toLowerCase() === 'input') {
          prev.focus();
          break;
        }
        prev = prev.previousElementSibling;
      }
    } else if (e.target.value.length === this.props.maxLength) {
      let next = e.target.nextElementSibling;

      while (next) {
        if (next === null) break;
        if (next.tagName.toLowerCase() === 'input') {
          next.focus();
          break;
        }
        next = next.nextElementSibling;
      }
    }
  };

  render() {
    const { info, meta, label, customClass } = this.props;
    const bemClasses = getBEMClasses(baseClass, customClass);

    const inputs = this.state.values.map((value, index) => (
      <input
        key={index}
        className={bemClasses()}
        onKeyUp={e => this.handleKeyUp(e, index)}
        onChange={e => this.handleChange(e, index)}
        value={this.state.values[index]}
      />
    ));

    return (
      <div className={'form-group'}>
        {this.props.type !== 'hidden' &&
          label && <label className={bemClasses('label')}>{label}</label>}
        {inputs}
        {info && <small className={'form-text-muted'}>{info}</small>}
        {meta.touched &&
          meta.error && (
            <span className={`${bemClasses('error')}`}>{meta.error}</span>
          )}
      </div>
    );
  }
}

const APAutoTabInput = props => (
  <Field name={props.name} component={InnerInput} {...props} />
);

APAutoTabInput.defaultProps = {
  maxLength: 1
};

APAutoTabInput.propTypes = {
  name: PropTypes.string.isRequired,
  type: PropTypes.string,
  customClass: PropTypes.string,
  numberOfInputs: PropTypes.number.isRequired,
  maxLength: PropTypes.number
};

export default APAutoTabInput;



