import React from 'react';
import PropTypes from 'prop-types';
import APAutoTabInput from '../common/APAutoTabInput/APAutoTabInput';
import path from '../../helpers/path';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { required } from '../../helpers/formValidations';
import { Redirect } from 'react-router-dom';

import '../../styles/components/common/StandaloneForm.css';
import './TwoFactorAuthFormComponent.css';

const baseClasses = getBEMClasses('standalone-form');
const twoFactorAuthFormClasses = getBEMClasses('two-factor-auth-form');

export const TwoFactorAuthFormComponent = (props, context) => {
  const { handleSubmit, is2FARequired, error } = props;

  if (!is2FARequired) return <Redirect to={path('/login')} />;

  return (
    <div
      className={`${baseClasses('container')} ${twoFactorAuthFormClasses(
        'container'
      )}`}>
      <div
        className={`${baseClasses('header')} ${twoFactorAuthFormClasses(
          'header'
        )}`}>
        <div
          className={`${baseClasses('header-text')} ${twoFactorAuthFormClasses(
            'header-text'
          )}`}>
          {context.t('Two-Factor Authentication')}
        </div>
      </div>
      <form
        onSubmit={handleSubmit}
        className={`${baseClasses('form')} ${twoFactorAuthFormClasses(
          'form'
        )}`}>
        <div
          className={`${baseClasses(
            'information-text'
          )} ${twoFactorAuthFormClasses('information-text')}`}>
          {context.t('Enter the token from your Authenticator app.')}
        </div>
        <div className={baseClasses('auto-tab-input')}>
          <APAutoTabInput
            name="code"
            type="input"
            numberOfInputs={6}
            validate={[required]}
            customClass={twoFactorAuthFormClasses('input')}
          />
          {error && (
            <div
              className={`${baseClasses('error')} ${twoFactorAuthFormClasses(
                'error'
              )}`}>
              {error}
            </div>
          )}
        </div>
      </form>
    </div>
  );
};

TwoFactorAuthFormComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  submitting: PropTypes.bool.isRequired
};

TwoFactorAuthFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default TwoFactorAuthFormComponent;



